

.list {
  display: flex;
  flex-direction: column;
  max-width: 400px;
  font-size: 1.2em;
  color: darkred;

  &__item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    &:not(:last-child) {
      padding-bottom: 5px;
      border-bottom: 3px solid green;
    }
  }
}